export default {
	props: {
		value: {
			type: Object,
			default: {}
		}
	},
	data() {
		return {
			tagsItem: []
		}
	},
	computed: {},
	mounted() {
		this.tagsItem = this.value.tags.replace(' ', '').replace(/'/g, '').replace('[', '').replace(']', '').split(',');
	},
}
